import React from 'react'
import {findDOMNode} from 'react-dom';
import cx from 'classnames'
import smoothScroll from 'smooth-scroll'

import ArtistCard from '../ArtistCard'
import ImageGrid from '../ImageGrid'
import InstrumentsBlock from '../InstrumentsBlock/'
import Instrument from '../Instrument/'

import './faculty.scss'

const SCROLLING_OPTIONS = {
	speed: 300,
	easing: 'easeInOutQuad',
	updateURL: false
}

const CATEGORIES_SELECTOR = '#faculty-categories'
const INSTRUMENTS_SELECTOR = '#faculty-instruments'
const ARTISTS_SELECTOR = '#faculty-artists'


class Faculty extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			categoryIndex: -1,
			instrumentIndex: -1,
			artistIndex: -1
		}
	}

	isCategorySelected() {
		return this.state.categoryIndex !== -1
	}

	isInstrumentSelected() {
		return this.state.instrumentIndex !== -1
	}

	isArtistSelected() {
		return this.state.artistIndex !== -1
	}

	scrollTo(selector) {
		smoothScroll.animateScroll(selector, null, SCROLLING_OPTIONS);
	}

	handleCategoryClick(index, event) {
		// toggle
		let categoryIndex = this.state.categoryIndex === index ? -1 : index

		this.setState({
			categoryIndex,
			instrumentIndex: -1,
			artistIndex: -1,
		}, () => {
			this.isCategorySelected() && this.scrollTo(CATEGORIES_SELECTOR);
		})
	}

	handleInstrumentClick(index, event) {
		// toggle
		let instrumentIndex = this.state.instrumentIndex === index ? -1 : index

		this.setState({
			instrumentIndex,
			artistIndex: -1,
		}, () => {
			this.isInstrumentSelected() && this.scrollTo(INSTRUMENTS_SELECTOR)
		})
	}

	handleArtistClick(index, event) {
		// toggle
		let artistIndex = this.state.artistIndex === index ? -1 : index
		
		this.setState({
			artistIndex
		}, () => {
			this.isArtistSelected() && this.scrollTo(ARTISTS_SELECTOR);
		})
	}

	render() {
		var items = this.props.items,
			categoryIndex = this.state.categoryIndex,
			instrumentIndex = this.state.instrumentIndex,
			artistIndex = this.state.artistIndex,
			instruments,
			artists,
			artist;

		// Return item markup for category of instruments
		let categoryMarkup = (category, index) => {
			return (
				<Instrument
						active={categoryIndex === index}
						title={category.category}
						icon={category.icon}
						key={index}
						onClick={this.handleCategoryClick.bind(this, index)}
					/>
				)
		}

		// Return item markup for instrument
		let instrumentsMarkup = (instrument, index) => {
			return (
				<Instrument
						inversed
						active={instrumentIndex === index}
						title={instrument.name}
						icon={instrument.icon}
						key={index}
						onClick={this.handleInstrumentClick.bind(this, index)}/>
			)
		}

		// Return item markup for artist thumb
		let artistThumbMarkup = (item, index) => {
			var classes = cx('artists-block-artist', {'artists-block-artist_selected': index === artistIndex});
			return (
				<li key={index} className={classes} onClick={this.handleArtistClick.bind(this, index)}>
					<div className="artists-block-artist__image" style={{backgroundImage: 'url(' + item.image + ')'}}/>
					<div className="artists-block-artist__name">{item.name}</div>
		 		</li>
			)
		}


		// list of categories of instruments
		let categories = items.map(categoryMarkup)
		// list of instruments in category
		// if there are an 'artists' array in category item, instruments won't shown
		if (this.isCategorySelected() && !(items[categoryIndex].artists instanceof Array)) {
			
			let instrumentsItems = items[categoryIndex].instruments.map(instrumentsMarkup)
			instruments = <InstrumentsBlock gray id="faculty-instruments">{instrumentsItems}</InstrumentsBlock>

		}
		// list of artists with selected instrument OR selected category
		if (this.isInstrumentSelected() || (this.isCategorySelected() && items[categoryIndex].artists instanceof Array)) {
			
			let artistsData = items[categoryIndex].artists || items[categoryIndex].instruments[instrumentIndex].artists

			if (artistsData.length === 0) {
				artists = <div className="artists-block__empty">Names of faculty members will be released on October 15, 2016</div>
			} else {
				artists = <ul className="artists-block" id="faculty-artists">{artistsData.map(artistThumbMarkup)}</ul>
			}
		}

		// artist detail info
		if (this.isArtistSelected()) {
			let artistData = (items[categoryIndex].artists && items[categoryIndex].artists[artistIndex])|| items[categoryIndex].instruments[instrumentIndex].artists[artistIndex];
			artist = (
				<ArtistCard
					image={artistData.image}
					title={artistData.name}
					subtitle={artistData.subtitle}
					description={artistData.description}
				/>
			)
		}

		return (
			<div className="faculty">
				<div className="wrapper-1000px wrapper-padding-top">
					<h2 className="middle-title">Faculty</h2>
					<p className="gray-text">
						The orchestra will spend much of its training residency preparing under NYO-China’s music director, Chen Lin, and later its guest conductor, Maestro Ludovic Morlot. They will be joined by a full staff of sectional coaches recruited from the principal members of major American orchestras, many of whom simultaneously pursue active teaching careers at eminent music conservatories across the United States. These outstanding faculty members will serve as active mentors for musicians throughout W Conservatory, providing a wealth of experience and connections from within the world of classical music.
					</p>
				</div>
				<InstrumentsBlock id="faculty-categories">{categories}</InstrumentsBlock>
				{instruments}
				<div className="wrapper-1000px">
					{artists}
					{/*artist*/}
				</div>
			</div>
		)
	}
}

export default class TrainingResidency extends React.Component {
	render() {
		return (
			<div>
				<div className="wrapper-1000px wrapper-padding-top">
					<p className="gray-text">
						The training residency of NYO-China is designed to develop individual members’ skills as musicians and to prepare the orchestra as a whole for its concert tour. Members of the orchestra will participate in a two-week program that consists of sectional rehearsals led by a world-class faculty of coaches and full orchestra rehearsals under the direction NYO-China’s music director and guest conductor. Members will also have access to other learning and exchange opportunities such as master classes, recreational chamber music readings, weekend concert excursions, and social interactions with NYO-USA.
					</p>
				</div>
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">
					<ImageGrid images={this.props.images}/>
				</div>
				<hr/>
				<Faculty items={this.props.faculty}/>
			</div>
		)
	}
}


